	.h_box {
		position: absolute;
		top: 20%;
		left: 50%;
		transform: translateX(-50%);
		width: 50%;
		height: 50%;
		animation: human 3000ms linear infinite;
		background: url(../../../public/img/hum.png) no-repeat center center/contain;
		img {
			max-width: 100%;
			height: 100%;
		}
	}
