@keyframes human {
	0% {
		top: 20%;
	}
	50% {
		top: 22%;
	}
	100% {
		top: 20%;
	}
}
@keyframes animStar {
	from {
		transform: translateY(0px);
	}
	to {
		transform: translateY(-2000px);
	}
}

@keyframes animStar {
	from {
		transform: translateY(0px);
	}
	to {
		transform: translateY(-2000px);
	}
}
